import React from 'react'
import Helmet from 'react-helmet'

import { SITE_URL } from '../constants'
import defaultMetaImage from '../../static/skovhus.jpg'

type Props = {
  description?: string
  image?: string
  meta: { name: string; content: string }[]
  title?: string
}

export default function SEO({ description, image, meta, title }: Props) {
  const metaDescription = description || 'Personal website and blog by Kenneth Skovhus.'

  const metaImageSrc = image || `${SITE_URL}/${defaultMetaImage}`

  if (!metaImageSrc.startsWith('https')) {
    throw new Error(`Invalid metaImageSrc ${metaImageSrc}`)
  }

  const siteTitle = 'Kenneth Skovhus'

  return (
    <Helmet
      htmlAttributes={{
        lang: 'en',
      }}
      title={title ? `${title} | ${siteTitle}` : siteTitle}
      meta={[
        {
          name: `description`,
          content: metaDescription,
        },
        {
          property: `og:title`,
          content: title,
        },
        {
          property: `og:description`,
          content: metaDescription,
        },
        {
          property: `og:type`,
          content: `website`,
        },
        {
          name: `twitter:card`,
          content: `summary`,
        },
        {
          name: `twitter:site`,
          content: `@kenneth_skovhus`,
        },
        {
          name: `twitter:creator`,
          content: '@kenneth_skovhus',
        },
        {
          name: `twitter:title`,
          content: title,
        },
        {
          name: `twitter:description`,
          content: metaDescription,
        },
        {
          name: 'twitter:image',
          content: metaImageSrc,
        },
        {
          name: 'og:image',
          content: metaImageSrc,
        },
      ].concat(meta)}
    />
  )
}

SEO.defaultProps = {
  meta: [],
}
